#contactMeArrow{
    position: fixed;
    display: grid;
    justify-items: center;
    align-items: center;
    font-size: calc((5vw + 5vh) / 2);
    width: 10vw;
    height: 5vh;
    left: calc(50% - 5vw);
    top: 0vh;
    color: #33c3f7;
    opacity: .5;
    z-index: 1001;
}
#contactMeArrow:hover{
    opacity: .8;
    font-size: calc((5.5vw + 5.5vh) / 2);
    -webkit-transition: opacity .5s ease-in-out;
    transition: opacity .5s ease-in-out;
    -webkit-animation: arrowbounce .5s ease;
            animation: arrowbounce .5s ease;
    /* animation-iteration-count: infinite; */
}
.contactMeArrowAnimation{
    -webkit-animation: arrowdown .5s ease-out !important;
            animation: arrowdown .5s ease-out !important;
}

#contactMeWord{
    position: fixed;
    display: grid;
    justify-items: center;
    align-items: center;
    font-size: calc((5vw + 5vh) / 2);
    z-index: 10;
    width: 20vw;
    height: 5vh;
    left: calc(50% - 1vw);
    top: 0vh;
}

@-webkit-keyframes arrowdown {
    0% {
        opacity: .8;
        top:0vh;
    }
    100% {
        opacity: 0;
        top: 15vh;
    }
}

@keyframes arrowdown {
    0% {
        opacity: .8;
        top:0vh;
    }
    100% {
        opacity: 0;
        top: 15vh;
    }
}

@-webkit-keyframes arrowbounce {
    0% {
        top: 0vh;
    }
    30% {
        top: 2vh;
    }
    60% {
        top: 0vh;
    }
    90% {
        top: 1vh;
    }
    100% {
        top: 0vh;
    }
}

@keyframes arrowbounce {
    0% {
        top: 0vh;
    }
    30% {
        top: 2vh;
    }
    60% {
        top: 0vh;
    }
    90% {
        top: 1vh;
    }
    100% {
        top: 0vh;
    }
}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: translateY(500%);
            transform: translateY(500%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slideIn {
  0% {
    -webkit-transform: translateY(500%);
            transform: translateY(500%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes shrink {
  0% {
    width: 0%;
  }
  100% {
    width: 200%;
  }
}

@keyframes shrink {
  0% {
    width: 0%;
  }
  100% {
    width: 200%;
  }
}
