.particleToggle {
  position: fixed;
  top: 5vh;
  left: 5vw;
  background: -webkit-radial-gradient(75% 25%, circle, white 1px, lightgreen 3%, darkgreen 60%, lightgreen 100%);
  background: radial-gradient(circle at 75% 25%, white 1px, lightgreen 3%, darkgreen 60%, lightgreen 100%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  z-index: 10;
  color: white;
  }
  .particleToggle:hover {
    cursor: crosshair;
  }
.particleToggle {
  color: white;
  margin-left: 2vw;
}
.greenToggle::after, .redToggle::after{
  position: absolute;
  left: 20px;
  top: -4px;
}
.greenToggle::after{
  content: 'on';
}
.redToggle::after{
  content: 'off';
}
